import { useTranslation } from "react-i18next";
import style from "../../../StoreManager/StoreManager.module.css";
import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import DepartmentSitePsoCardMenu from "./DepartmentSitePsoCardMenu";
const DepartmentSitePsoCard = ({ pso, onRefetch, dsid }) => {
  const { t } = useTranslation();
  const [open, setOpen] = useState(false);
  const onToggle = () => {
    setOpen((pre) => !pre);
  };
  return (
    <>
      <div
        className={style.smc_card}
        style={{
          alignItems: "flex-start",
        }}
      >
        <div className={style.smc_card_contatiner}>
          <div className={style.smc_card_contatiner}>
            <div className={style.smc_card_contatiner_inner}>
              <h6>
                {" "}
                {t("name")} : {pso?.title}
              </h6>
              <p>{pso?.description ?? ""}</p>
            </div>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onToggle}
        />
      </div>
      {open && (
        <DepartmentSitePsoCardMenu
          onClose={onToggle}
          dsid={dsid}
          menuData={pso}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default DepartmentSitePsoCard;
