import { useTranslation } from "react-i18next";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useState } from "react";

import {
  useDepartmentPsoRemove,
  useDepartmentPsoUpdate,
} from "../../../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddDepartmentSitePso from "./AddDepartmentSitePso";
const DepartmentSitePsoCardMenu = ({ dsid, onClose, menuData, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");

  const [departmentPsoRemove] = useDepartmentPsoRemove();
  const [departmentPsoUpdate] = useDepartmentPsoUpdate();
  const onStepClose = () => {
    setStep("");
  };

  const onDelete = () => {
    if (menuData?._id && dsid) {
      setDisabled((pre) => !pre);
      departmentPsoRemove({
        dsid: dsid,
        bodyParameter: {
          cid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onEditAction = (data) => {
    if (menuData?._id && dsid) {
      onStepClose();
      setDisabled((pre) => !pre);
      departmentPsoUpdate({
        dsid: dsid,
        bodyParameter: {
          ...data,
          cid: menuData?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  return (
    <>
      {step === 1 ? (
        <AddDepartmentSitePso
          isEdit
          dsid={dsid}
          onEditAction={onEditAction}
          menuData={menuData}
          onClose={onStepClose}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={() => setStep(1)}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default DepartmentSitePsoCardMenu;
